import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
import uploadImage from '@/components/common/upload-image.vue';
export default {
	components:{
		uniNavBar,
		uploadImage
	},
	data() {
		return {
			content:"",
			imageList:[],
			// 是否已经弹出提示框
			showBack:false
		}
	},
	computed: {
		show() {
			return this.imageList.length > 0
		}
	},
	// 页面加载时
	onLoad() {
		uni.getStorage({
			key:'add-input',
			success:(res)=>{
				if (res.data) {
					let result = JSON.parse(res.data);
					this.content = result.content;
					this.imageList = result.imageList;
				}
			}
		})
	},
	// 监听返回
	onBackPress() {
		if ((this.content !== '' || this.imageList.length > 0) && !this.showBack) {
			uni.showModal({
				content: '是否要保存为草稿？',
				showCancel: true,
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					// 点击确认
					if (res.confirm) {
						this.store();
					} else { // 点击取消，清除缓存
						uni.removeStorage({
							key: "add-input"
						})
					}
					// 手动执行返回
					uni.navigateBack({ delta: 1 });
				},
			});
			this.showBack = true
			return true
		}
	},
	methods: {
		// 底部图标点击事件
		iconClickEvent(e){
			switch (e){
				case 'uploadImage':
				this.$refs.uploadImage.chooseImage()
					break;
			}
		},
		// 返回上一页
		onClickLeft(){
			uni.navigateBack({ delta: 1 });
		},
		// 选中图片
		changeImage(e){
			this.imageList = e;
		},
		// 保存操作
		store(){
			// 保存为本地存储
			let obj = {
					content:this.content,
					imageList:this.imageList
				}
			uni.setStorage({
				key:'add-input',
				data:JSON.stringify(obj)
			})
		}
	}
}
